@import '../../../../commonStyle/variable';

.required {

  label {
    position: relative;

    &::before {
      position: absolute;
      content: '*';
      top: -1px;
      right: -7px;
    }
  }
}

.custom-select-wrap {
  width: 100%;
  margin-top: 20px;


  &__label {
    display: inline-block;
    margin-bottom: 10px;
    font-weight: normal;
    font-size: 14px;
    color: $black;
  }

  &>span {
    display: inline-block;
    margin-top: 8px;
    color: $red;
    font-size: 14px;
  }
}

.custom-select-label {
  color: var(--gray-80, #707070);
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  /* 150% */
  display: block;
}

.CustomSelect {

  .custom-select {
    &__menu {
      min-width: 200px !important;
      z-index: 999;
      position: absolute;
    }

    &__single-value {
      margin: 0;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 24px;
      color: $black  !important;
    }

    &__control {
      border: none !important;

      border-radius: 0 !important;
      padding-bottom: 15px;
      padding-top: 4px;

      &--is-disabled {
        background: #DDE7ED !important;

      }

      cursor: pointer;
      min-height: 36px;
      background: #FFFFFF;
      border: 1px solid $border-area;
      border-radius: 4px;
      font-size: 14px;
      font-weight: 300;

      &:hover {
        border: 1px solid $border-area;
      }

      &--is-focused {
        outline: none;
        background: $white;
        box-shadow: 0 0 6px rgba(71, 164, 227, 0.08);
        border: 1px solid $border-area-focus;
      }
    }

    &__indicators {}

    &__dropdown-indicator {
      position: relative;
      color: #A4C1D6;
      z-index: 0;

      &:before {
        top: 0;
        content: '';
        position: absolute;
        width: 20px;
        height: 20px;
        right: 0px;
        background: center/cover no-repeat url("../../../../assets/svg/arrow.svg");
      }


      & svg {
        display: none;
      }

      &:hover {
        color: #A4C1D6;
      }
    }

    &__indicator {
      padding: 6px;
    }

    &__indicator-separator {
      display: none;
    }

    &__option {
      font-size: 14px;
      font-weight: 300;
      color: $black  !important;

      &:hover {
        background-color: $blue-lighter;
        color: $blue-200  !important;
      }

      &--is-focused {
        background-color: transparent;
      }

      &--is-selected {
        background: $white;
        color: $black;

        &:hover {
          color: $black;
          background-color: $blue-lighter;
        }
      }
    }

    &__value-container {
      padding: 0;
    }

    &__placeholder {
      font-size: 14px;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 24px;
      /* 150% */
      margin: 0;
    }
  }
}

.err_msg {
  display: inline-block;
  margin-top: 8px;

  color: $red;
  font-size: 14px;
}

.invalid {
  border-bottom: red 1px solid;
}

.valid {
  input {

    color: black;
  }

  border-bottom: #707070 1px solid !important;

}